<template>
	<div class="pay-page">
		支付金额：0.01元
		<div>
			<van-button type="primary" @click="onBridgeReady">立即支付</van-button>
		</div>
	</div>
</template>

<script>
	import { Dialog } from 'vant';
	export default{
		methods:{
			onBridgeReady(){
				this.http.get('wx/orders',null,res=>{
					WeixinJSBridge.invoke('getBrandWCPayRequest', res.data,(res)=>{
						if(res.err_msg == "get_brand_wcpay_request:ok" ){
							Dialog.alert({
								message: '支付成功',
							});
						}else if(res.err_msg=='get_brand_wcpay_request:cancel'){
							Dialog.alert({
								message: '支付取消',
							});
						}else{
							Dialog.alert({
								message: '支付失败',
							});
						}
					});
					/*
						{
							"appId" : appId,     //公众号名称，由商户传入     
							"timeStamp": timeStamp,         //时间戳，自1970年以来的秒数     
							"nonceStr" : nonceStr, //随机串     
							"package" : packAge,     
							"signType" : signType,         //微信签名方式:     
							"paySign" : paySign    //微信签名 
						}
					*/
					
				})
				
			}
		}
	}
</script>

<style scoped lang="scss">
	.pay-page{
		text-align: center;
	}
</style>
